@import url(https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&display=swap);
:root {
  --onyx: #32373bff;
  --feldgrau: #4a5859ff;
  --feldgrau2: rgb(103, 117, 117);
  --unbleached-silk: #f4d6ccff;
  --sunray: #f4b860ff;
  --brick-red: #c83e4dff;
  --my-green: rgb(62, 110, 110);
}
* {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
body::-webkit-scrollbar {
  display: none;
}
body {
  margin: 0;
  color: #f4d6ccff;
  color: var(--unbleached-silk);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(#4a5859ff, #32373bff);
  background: radial-gradient(var(--feldgrau), var(--onyx));
  background-size: cover;
  height: 100vh;
  overflow-x: hidden;
  width: 100vw;
}
::-webkit-scrollbar-track {
  background: #000;
  border: 0px none #ffffff;
  border-radius: 53px;
}
button {
  background-color: #4a5859ff;
  background-color: var(--feldgrau);
  color: #f4d6ccff;
  color: var(--unbleached-silk);
  border-radius: 4px;
  height: 32px;
}
input {
  background-color: #f4d6ccff;
  background-color: var(--unbleached-silk);
  margin: 4px;
  border-radius: 4px;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.app__header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 130px;
  background: linear-gradient(80deg,#32373bff,#32373b00);
  border-bottom: 2px solid #21262aff;
  margin-bottom: 12px;
  padding-left: 32px;
  padding-top: 6px;
  -webkit-align-content: center;
          align-content: center;
  letter-spacing: 8px;
}
.app__header h1 {
  font-family: 'Zen Tokyo Zoo', cursive;
  margin: 0;
  font-size: 38px;
  background: linear-gradient(75deg, rgb(250, 178, 150), #f4d6ccff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.app__header .header__mob {
  font-size: 56px;
}
.app__main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 32px;
  -webkit-align-content: center;
          align-content: center;
}
.app__settings {
  -webkit-align-self: center;
          align-self: center;
  margin-bottom: 12px;
  height: 176px;
  transition: height 0.2s ease;
  border-bottom: 1px solid #32373bff;
  border-bottom: 1px solid var(--onyx);
  overflow: hidden;
}
.app__settings__inner input {
  margin-top: 12px;
}
.app__settings__newUserForm input {
  margin-bottom: 12px;
}
.settings__showhide {
  -webkit-align-self: center;
          align-self: center;
  width: 160px;
  margin-bottom: 12px;
}
.app__controls {
  -webkit-align-self: center;
          align-self: center;
  display:-webkit-flex;
  display:flex;
  width: 300px;
  -webkit-justify-content:space-between;
          justify-content:space-between;
}
.hidden {
  height: 0;
  border: none;
}
.app__userList {
  -webkit-align-self: center;
          align-self: center;
  min-width: 300px;
  padding: 12px;
  list-style: none;
  border-radius: 5px;
  background-color: rgb(103, 117, 117);
  background-color: var(--feldgrau2);
}
.userList__entry {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 56px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.userList__left {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.userList__right {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.userList__time {
  font-size: 22px;
  margin-right: 6px;
}
.userList__name {
  color: ivory;
}
.userList__removeUser {
  width: 32px;
  height: 32px;
  float:right;
}
.userList__name--currentUser {
  border: 3px solid rgb(62, 110, 110);
  border: 3px solid var(--my-green);
  border-radius: 12px;
  padding: 4px;
}
.userList__nousers {
  justify-self: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

